<template>
  <div><v-snackbar
        v-model="alert"
        timeout="5000"
      >
        <v-icon color="deep-orange darken-3">mdi-home</v-icon>
        {{ alertMsg }}
        
        <template v-slot:action="{ attrs }">
          <v-btn
            color="red"
            text
            v-bind="attrs"
            @click="alert = false"
          >
            Close
          </v-btn>
        </template>
      </v-snackbar></div>
</template>

<script>
import http from "@/util/http-common";
export default {
  name: "QnAdelete",
  data: function () {
    return {
      alert : false,
      alertMsg : "",
    }
  },
  created() {
    http
      .delete(`/qna/${this.$route.query.qnaNo}`)
      .then(({ data }) => {
        let msg = "   .";
        if (data === "success") {
          msg = " .";
        }
        this.alertMsg = msg;
        this.alert = true;
        this.$router.push("/qna");
      })
      .catch(() => {
        this.alertMsg = "   .";
        this.alert = true;
      });
  }
};
</script>

