<template>
  <div align="center">
    <h1 class="display-2 font-weight-bold mb-3">RECOMMENDED LIST</h1>
      <v-sheet color="#6A76AB" elevation="24" rounded="pill" style="color:white;" height="25" width="80%">     .</v-sheet>
      <br>
    <div class="map_wrap">
      <div id="map" style="width:100%;height:100%;position:relative;overflow:hidden;"></div>
      <ul id="category" v-if="items.length">
        <li id="BK9" data-order="0">
          <span class="category_bg bank"></span>
          
        </li>
        <li id="MT1" data-order="1">
          <span class="category_bg mart"></span>
          
        </li>
        <li id="PM9" data-order="2">
          <span class="category_bg pharmacy"></span>
          
        </li>
        <li id="OL7" data-order="3">
          <span class="category_bg oil"></span>
          
        </li>
        <li id="CE7" data-order="4">
          <span class="category_bg cafe"></span>
          
        </li>
        <li id="CS2" data-order="5">
          <span class="category_bg store"></span>
          
        </li>
      </ul>
    </div>
    <v-row justify="left">
      <v-col cols="1">
        <v-select :items="searchItems" v-model="selected" label=""></v-select>
      </v-col>
      <v-col cols="2">
        <v-text-field label="  " v-model="price" @keyup="searchL" v-show="searchShow"></v-text-field>
      </v-col>
    </v-row>

    <div v-if="items.length">
      <v-simple-table>
        <template v-slot:default>
          <colgroup>
            <col :style="{ width: '7%' }" />
            <col :style="{ width: '24%' }" />
            <col :style="{ width: '7%' }" />
            <col :style="{ width: '7%' }" />
            <col :style="{ width: '7%' }" />
            <col :style="{ width: '10%' }" />
            <col :style="{ width: '7%' }" />
            <col :style="{ width: '7%' }" />
            <col :style="{ width: '7%' }" />
            <col :style="{ width: '10%' }" />
          </colgroup>
          <thead>
            <tr>
              <th class="text-left"></th>
              <th class="text-left"></th>
              <th class="text-left"></th>
              <th class="text-left"></th>
              <th class="text-left"></th>
              <th class="text-left"></th>
              <th class="text-left"></th>
              <th class="text-left"></th>
              <th class="text-left"></th>
              <th class="text-left"></th>
            </tr>
          </thead>
          <tbody>
            <list-row
              v-for="(item, index) in items"
              :key="`${index}_items`"
              :no="item.no"
              :dong="item.dong"
              :aptName="item.aptName"
              :code="item.code"
              :dealAmount="item.dealAmount"
              :buildYear="item.buildYear"
              :dealYear="item.dealYear"
              :dealMonth="item.dealMonth"
              :dealDay="item.dealDay"
              :area="item.area"
              :floor="item.floor"
              :jibun="item.jibun"
              :type="item.type"
              v-on:showModal="showModal"
            />
          </tbody>
        </template>
      </v-simple-table>
      <div style="padding-center : 200px" align="center">
        <v-btn text small color="primary" @click="pageMove(1)"></v-btn>
        <v-btn text small color="primary" @click="pageMove(priv)"></v-btn>
        <span v-for="index in indexes" v-bind:key="index">
          <v-btn
            flat
            small
            @click="pageMove(startPage+index-1)"
            v-if="index==1"
          >{{startPage+index-1}}</v-btn>
          <v-btn text small @click="pageMove(startPage+index-1)" v-else>{{startPage+index-1}}</v-btn>
        </span>
        <v-btn text small color="primary" @click="pageMove(next)"></v-btn>
        <v-btn text small color="primary" @click="pageMove(end)"></v-btn>
      </div>
    </div>
    <v-dialog v-model="dialog" max-width="600">
        <v-card>
          <v-card-title class="headline">  </v-card-title>
          <detail
            :no="item.no"
            :dong="item.dong"
            :aptName="item.aptName"
            :code="item.code"
            :dealAmount="item.dealAmount"
            :buildYear="item.buildYear"
            :dealYear="item.dealYear"
            :dealMonth="item.dealMonth"
            :dealDay="item.dealDay"
            :area="item.area"
            :floor="item.floor"
            :jibun="item.jibun"
            :type="item.type"
          />
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn color="green darken-1" flat @click.native="dialog = false"></v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>

      <v-snackbar
        v-model="alert"
        timeout="5000"
      >
        <v-icon color="deep-orange darken-3">mdi-home</v-icon>
        {{ alertMsg }}
        
        <template v-slot:action="{ attrs }">
          <v-btn
            color="red"
            text
            v-bind="attrs"
            @click="alert = false"
          >
            Close
          </v-btn>
        </template>
      </v-snackbar>
  </div>
</template>

<script>
import http from "@/util/http-common";
import ListRow from "@/components/HouseRow.vue";
import Detail from "@/components/HouseDetail.vue";
export default {
  name: "recommendList",
  components: {
    ListRow,
    Detail,
  },
  data: function() {
    return {
      item: {},
      items: [],
      selected: "",
      price: "",
      nav: "",
      startPage: "",
      endPage: "",
      indexes: "",
      searchItems:[],
      searchShow: false,
      dialog: false,
      alert: false,
      alertMsg: "",
    };
  },
  computed: {
    priv() {
      return this.nav.startRange ? 1 : this.startPage - 1;
    },
    next() {
      return this.nav.endRange ? this.endPage : this.endPage + 1;
    },
    end() {
      return this.nav.totalPageCount;
    }
  },
  watch: {
    selected : function() {
        this.searchShow = true;
    }
  },
  created() {
    http.get(`/recommend`).then(({ data }) => {
      this.searchItems = data;
      console.dir(data);
      window.kakao && window.kakao.maps ? this.initMap() : this.addScript();
    });
  },
  methods: {
    showModal(no) {
        http.get(`/house/${no}`).then(({ data }) => {
            this.item = data;
        });
        this.dialog = true;
    },
    pageMove(pg) {
      http
        .post("/recommend", {
          dong: this.selected,
          dealAmount: this.price / 1000,
          currentPage: pg,
          sizePerPage: 10
        })
        .then(({ data }) => {
          this.items = data;
          window.kakao && window.kakao.maps ? this.initMap() : this.addScript();
        })
        .catch(() => {
          this.alertMsg = "   ";
          this.alert=true;
        });
      http
        .put("/recommend", {
          dong: this.selected,
          dealAmount: this.price / 1000,
          currentPage: pg,
          sizePerPage: 10
        })
        .then(({ data }) => {
          this.nav = data;
          this.startPage =
            ((this.nav.currentPage - 1) / this.nav.naviSize) *
              this.nav.naviSize +
            1;
          this.endPage = this.startPage + this.nav.naviSize - 1;
          if (this.nav.totalPageCount < this.endPage)
            this.endPage = this.nav.totalPageCount;
          this.indexes = this.endPage - this.startPage + 1;
        })
        .catch(() => {
          this.alertMsg = "  .";
          this.alert= true;
        });
    },

    search() {
      this.pageMove(1);
    },

    searchL(e) {
      if (e.which == 13) {
        this.search();
      }
    },

    initMap() {
      //        
      var placeOverlay = new kakao.maps.CustomOverlay({ zIndex: 1 }),
        contentNode = document.createElement("div"), //     
        markers = [], //   
        currCategory = ""; //      

      var container = document.getElementById("map");
      var options = {
        center: new kakao.maps.LatLng(37.5642135, 127.0016985),
        level: 8
      };
      var map = new kakao.maps.Map(container, options);

      var bounds = new kakao.maps.LatLngBounds();
      for (var i = 0; i < this.items.length; i++) {
        //  
        var marker = new kakao.maps.Marker({
          map: map, //   
          position: new kakao.maps.LatLng(this.items[i].lat, this.items[i].lng), //   
          title: this.items[i].aptName, //  ,     
          clickable: true
        });
        // LatLngBounds   
        bounds.extend(
          new kakao.maps.LatLng(this.items[i].lat, this.items[i].lng)
        );
        var content =
          '<div>' +
          this.items[i].aptName +
          "</div>";
        //    
        var infowindow = new kakao.maps.InfoWindow({
          content: content //   
        });
        //         
        //        
        (function(marker, infowindow) {
          //   
          kakao.maps.event.addListener(marker, "click", function() {
            //    
            infowindow.open(map, marker);
          });
        })(marker, infowindow);
      }
      marker.setMap(map);
      map.setBounds(bounds);
      
      ////////////////////////////////////////////////////////////

      //    
      var ps = new kakao.maps.services.Places(map);

      //  idle  
      kakao.maps.event.addListener(map, "idle", searchPlaces);

      //     css class 
      contentNode.className = "placeinfo_wrap";

      //     mousedown, touchstart  
      //        kakao.maps.event.preventMap  
      addEventHandle(contentNode, "mousedown", kakao.maps.event.preventMap);
      addEventHandle(contentNode, "touchstart", kakao.maps.event.preventMap);

      //    
      placeOverlay.setContent(contentNode);

      //     
      addCategoryClickEvent();
      
      //     
      function addEventHandle(target, type, callback) {
        if (target.addEventListener) {
          target.addEventListener(type, callback);
        } else {
          target.attachEvent("on" + type, callback);
        }
      }

      //    
      function searchPlaces() {
        if (!currCategory) {
          return;
        }

        //   
        placeOverlay.setMap(null);

        //     
        removeMarker();

        ps.categorySearch(currCategory, placesSearchCB, { useMapBounds: true });
      }

      //      
      function placesSearchCB(data, status) {
        if (status === kakao.maps.services.Status.OK) {
          //      
          displayPlaces(data);
        } else if (status === kakao.maps.services.Status.ZERO_RESULT) {
          //        
        } else if (status === kakao.maps.services.Status.ERROR) {
          //            
        }
      }

      //    
      function displayPlaces(places) {
        //     
        //       
        var order = document
          .getElementById(currCategory)
          .getAttribute("data-order");

        for (var i = 0; i < places.length; i++) {
          //    
          var marker = addMarker(
            new kakao.maps.LatLng(places[i].y, places[i].x),
            order
          );

          //      
          //     
          (function(marker, place) {
            kakao.maps.event.addListener(marker, "click", function() {
              displayPlaceInfo(place);
            });
          })(marker, places[i]);
        }
      }

      //       
      function addMarker(position, order) {
        var imageSrc =
            "https://t1.daumcdn.net/localimg/localimages/07/mapapidoc/places_category.png", //   url,   
          imageSize = new kakao.maps.Size(27, 28), //   
          imgOptions = {
            spriteSize: new kakao.maps.Size(72, 208), //   
            spriteOrigin: new kakao.maps.Point(46, order * 36), //       
            offset: new kakao.maps.Point(11, 28) //      
          },
          markerImage = new kakao.maps.MarkerImage(
            imageSrc,
            imageSize,
            imgOptions
          ),
          marker = new kakao.maps.Marker({
            position: position, //  
            image: markerImage
          });

        marker.setMap(map); //    
        markers.push(marker); //    

        return marker;
      }

      //       
      function removeMarker() {
        for (var i = 0; i < markers.length; i++) {
          markers[i].setMap(null);
        }
        markers = [];
      }

      //         
      function displayPlaceInfo(place) {
        var content =
          '<div class="placeinfo">' +
          '   <a class="title" href="' +
          place.place_url +
          '" target="_blank" title="' +
          place.place_name +
          '">' +
          place.place_name +
          "</a>";

        if (place.road_address_name) {
          content +=
            '    <span title="' +
            place.road_address_name +
            '">' +
            place.road_address_name +
            "</span>" +
            '  <span class="jibun" title="' +
            place.address_name +
            '">( : ' +
            place.address_name +
            ")</span>";
        } else {
          content +=
            '    <span title="' +
            place.address_name +
            '">' +
            place.address_name +
            "</span>";
        }

        content +=
          '    <span class="tel">' +
          place.phone +
          "</span>" +
          "</div>" +
          '<div class="after"></div>';

        contentNode.innerHTML = content;
        placeOverlay.setPosition(new kakao.maps.LatLng(place.y, place.x));
        placeOverlay.setMap(map);
      }

      //     
      function addCategoryClickEvent() {
        console.log(1);
        var category = document.getElementById("category"),
          children = category.children;

        for (var i = 0; i < children.length; i++) {
          children[i].onclick = onClickCategory;
        }
      }

      //     
      function onClickCategory() {
        var id = this.id,
          className = this.className;

        placeOverlay.setMap(null);

        if (className === "on") {
          currCategory = "";
          changeCategoryClass();
          removeMarker();
        } else {
          currCategory = id;
          changeCategoryClass(this);
          searchPlaces();
        }
      }

      //      
      function changeCategoryClass(el) {
        var category = document.getElementById("category"),
          children = category.children,
          i;

        for (i = 0; i < children.length; i++) {
          children[i].className = "";
        }

        if (el) {
          el.className = "on";
        }
      }
    },

    addScript() {
      const script = document.createElement("script");
      /* global kakao */ script.onload = () => kakao.maps.load(this.initMap);
      script.src =
        "http://dapi.kakao.com/v2/maps/sdk.js?autoload=false&libraries=services&appkey=c0bc706b6bc821a05c4b8cd8bdfe570a";
      document.head.appendChild(script);
    }
  }
};
</script>
<style scoped>
th {
  background-color:#eeeeee; 
  color:#3e5fba;
}
</style>